"use client"

import React from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';

const Navbar: React.FC = () => {
  const router = useRouter();

  // Handlers for button actions
  const handleLogoClick = () => {
    router.push('/'); // Refresh the page or navigate to the home page
  };

  const handleNavigate = (path: string) => {
    router.push(path); // Navigate to the given path
  };

  const handleAccountClick = () => {
    console.log("Account button clicked"); // Placeholder for account actions
    router.push('/account');
  };

  return (
    <nav className="flex items-center justify-between bg-blue-600 px-6 py-3 h-16">
      {/* Logo Button */}
      <Link className="flex items-center space-x-2" href="/">
        <div className="text-blue-300 font-bold text-lg">GeoQuest</div>
      </Link>
      
      {
      /*
            <button onClick={handleLogoClick} className="flex items-center space-x-2">
              <div className="text-blue-300 font-bold text-lg">GeoQuest</div>
            </button>
      */
      }

      {/* Navigation Buttons */}
      <div className="flex space-x-6">
        <button
          onClick={() => handleNavigate('/game')}
          className="text-white hover:text-blue-300 transition"
        >
          Игра
        </button>
        <button
          onClick={() => handleNavigate('/leaderboard')}
          className="text-white hover:text-blue-300 transition"
        >
          Класация
        </button>
        <button
          onClick={() => handleNavigate('/about')}
          className="text-white hover:text-blue-300 transition"
        >
          За нас
        </button>
      </div>

      {/* Account Button */}
      <button
        onClick={handleAccountClick}
        className="w-8 h-8 bg-gray-300 rounded-full flex items-center justify-center"
      >
        <span className="sr-only">Account</span>
      </button>
    </nav>
  );
};

export default Navbar;
